.form_checkbox__label {
  position: relative;
  padding-left: 3rem;
  font-size: 1.6rem;
  color: #000;
  cursor: pointer;
}
.form_checkbox__label:before, .form_checkbox__label:after {
  position: absolute;
  top: -1px;
  left: 0;
  width: 2rem;
  height: 2rem;
  cursor: pointer;
}
.form_checkbox__label:before {
  content: "";
  border-radius: 2px;
  border: 1px solid #000;
}
.form_checkbox__label:after {
  text-align: center;
  line-height: 2rem;
  opacity: 0;
  color: #000;
}
.form_checkbox__element:checked + .form_checkbox__label:after {
  opacity: 1;
}

.form_radio__label {
  position: relative;
  padding-left: 3rem;
  font-size: 1.6rem;
  color: #000;
  cursor: pointer;
}
.form_radio__label:before {
  content: "";
  position: absolute;
  top: -1px;
  left: 0;
  width: 2rem;
  height: 2rem;
  border-radius: 100%;
  border: 1px solid #000;
  cursor: pointer;
}
.form_radio__label:after {
  content: "";
  position: absolute;
  top: 0.4rem;
  left: 0.5rem;
  width: 1rem;
  height: 1rem;
  border-radius: 100%;
  background: #000;
  opacity: 0;
}
.form_radio__element:checked + .form_radio__label:after {
  opacity: 1;
}

